* {
  box-sizing: border-box;
}

.home-page {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
}

.img__logo {
  display: flex;
  width: 40px;
  height: 40px;
  padding: 0;
  justify-self: center;
  margin: 10px 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  width: getWidth($card-number);
  padding-top: 50px;
}
